import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import BottomBar from "./BottomBar";
import AppBar from "./AppBar";
import Cards from "./Card2";

import "./project.css";

const styles = {
  card: {
    maxWidth: 345,
    width: 300,
    margin: "0 auto"
  },
  media: {
    height: 140
  },
  size: {
    display: "flex",
    flexWrap: "wrap",
    alignItem: "space-between"
  }
};

function MediaCard(props) {
  const { classes } = props;
  return (
    <div className="bottomBar">
      <div>
        <AppBar />
      </div>
      <div className="pa">
        <div
          style={{
            background: "white",
            color: "white",
            width: "83%",
            margin: "0 auto",
            marginTop: "5%",
            padding: "0% 5% 2% 5%",
            borderRadius: "1%"
          }}>
          <h1
            style={{ textAlign: "center", paddingTop: "20px", color: "black" }}>
            PWA using React.js
          </h1>

          <Grid
            style={{
              display: "flexbox",
              alignItem: "space-between",
              justifyContent: "space-between"
            }}
            container
            spacing={24}>
            <Grid item md={3} xs={12}>
              <Cards
                name="HopeLine"
                description="This is a Hospital Finder App made using React.js,Redux.js and and backend with Node.js and MongoDB"
                image="https://github.com/Pravash2/Find_Hospital/raw/master/ezgif-1-bd0ab7a87fe1.gif"
                code="https://github.com/Pravash2/Find_Hospital/tree/master"
                demo="https://find-hospitals.herokuapp.com/"
              />
            </Grid>
            <Grid item md={3} xs={12}>
              <Cards
                name="SymptomsApp"
                description="This a symptoms cheaker PWA.It is used to find the Specialization and disgonosis of disease based upon body symptoms"
                image="https://github.com/Pravash2/SymptomsApp/raw/master/RhnyDXKoDp.gif"
                code="https://github.com/Pravash2/SymptomsApp"
                demo="https://github.com/Pravash2/SymptomsApp"
              />
            </Grid>

            <Grid item md={3} xs={12}>
              <Cards
                name="HostelApp"
                description="This is the Full stack App made upon React ,MaterialUI ,redux ,node.js ,MongoDB.This is a college autonomous...."
                image="https://github.com/Pravash2/HostelApp2/raw/master/ezgif-1-9ac6ec0ce26a.gif"
                code="https://github.com/Pravash2/HostelApp2"
                demo="https://hostel3.herokuapp.com/"
              />
            </Grid>
          </Grid>
        </div>
      </div>
      <BottomBar />
    </div>
  );
}

MediaCard.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(MediaCard);
