import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import AppBar from "./AppBar";
import Profile from "../image/profile.png";
import Skills2 from "./skills2";
import Card from "./Card";

import "./contact.css";
import BottomBar from "./BottomBar";
import { List, ListItem, TextField, Button } from "@material-ui/core";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  },
  marginLeft: {
    marginLeft: 20
  }
});

function CenteredGrid(props) {
  const { classes } = props;

  return (
    <div id="back" className={classes.root}>
      <div className="AppBar">
        <AppBar />
      </div>
      <BottomBar />
      <div>
        <Grid
          style={{
            background: "white",
            padding: "1em",
            margin: "0 auto",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            width: "93%",
            marginTop: "5%",
            height: "80vh",
            borderRadius: "1%"
          }}
          container
          spacing={12}>
          <Grid className="close" item md={6}>
            <div
              style={{ margin: "0 auto", width: "100%", textAlign: "center" }}>
              <h1>Pravash Kumar Behera</h1>
              <img
                className="profileImage"
                width={window.innerHeight / 3}
                style={{ borderRadius: "50%" }}
                src={Profile}
              />
              <div className="contact-list" style={{ marginLeft: "25%" }}>
                <List style={{ padding: "0", margin: "0" }}>
                  <ListItem style={{ fontSize: "30px", fontFamily: "Anton" }}>
                    <i className="fa fa-phone-square" aria-hidden="true" />
                    &nbsp;(+91) 965-888-4153
                  </ListItem>

                  <ListItem style={{ fontSize: "29.5px", fontFamily: "Anton" }}>
                    <i className="fa fa-envelope" aria-hidden="true" />
                    &nbsp;pravashbehera2@gmail.com
                  </ListItem>
                </List>
              </div>
            </div>
          </Grid>
          <Grid
            style={{
              textAlign: "center",
              color: "white",
              padding: "1rem 1rem",
              border: "5px solid #8839AE",
              borderRadius: "2%"
            }}
            item
            md={6}>
            <div style={{ width: "100%" }}>
              <h1 style={{ color: "#777" }}>Contact Me</h1>
              <TextField
                id="standard-name"
                label="Name"
                className={classes.textField}
                type="text"
                fullWidth
                margin="normal"
              />
              <TextField
                id="standard-name"
                label="Email"
                className={classes.textField}
                type="email"
                fullWidth
                margin="normal"
              />{" "}
              <TextField
                id="standard-multiline-static"
                label="Message"
                multiline
                rows="4"
                className={classes.textField}
                margin="normal"
                fullWidth
              />
              <Button
                style={{
                  backgroundColor: "#8539B0",
                  width: "50%",
                  marginTop: "20px",
                  color: "white"
                }}
                fullWidth
                variant="contained">
                Submit
              </Button>
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CenteredGrid);
