import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import AppBar from "./AppBar";
import BottomBar from "./BottomBar";
import "./about.css";
import { Link } from "react-router-dom";
import Button from "@material-ui/core/Button";
import RubberBand from "react-reveal/RubberBand";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  }
});

function CenteredGrid(props) {
  const { classes } = props;

  return (
    <div id="small" className={classes.root}>
      <div className="AppBar">
        <AppBar />
      </div>
      <BottomBar />
      <div className="education">
        <Grid
          style={{
            background: "white",
            padding: "1em",
            margin: "0 auto",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            borderRadius: "1%",
            width: "93%",
            marginTop: "5%"
          }}
          className="grid"
          container
          spacing={5}>
          <Grid style={{ padding: "1em" }} item md={4}>
            <RubberBand duration={6000} delay={1000}>
              <Link
                className="rubberBand"
                to="route"
                download
                onClick={event => {
                  event.preventDefault();
                  window.open(
                    "https://drive.google.com/uc?authuser=0&id=1U4Po1lUp-kxXqaI63txfK51Qk9WpiF8K&export=download"
                  );
                }}
                style={{
                  textDecoration: "none",
                  margin: "0 auto",
                  textAlign: "center"
                }}>
                <Button variant="contained" color="secondary">
                  Resume
                </Button>
              </Link>
            </RubberBand>
            <h5
              style={{
                textAlign: "center",
                fontSize: "1rem",
                fontWeight: "blod"
              }}>
              <br />| Pravash Kumar Behera |
            </h5>
            <h3>Programmer</h3>
            <hr
              style={{
                borderTop: "3px solid #833fb2",
                width: "50%",
                marginLeft: "0"
              }}
            />
            <p>
              Full stack web and mobile app developer with a flair for creating
              elegant solutions in the least amount of time and responsible for
              end-to-end web app development and creative cloud engineering.
              Developed an Ecommerce web app, Health are portal,Native Hostel
              App and donations web app for a local charity.Regular attendee of
              web developer meetups and Hackathons.
            </p>
            <hr
              style={{
                borderTop: "3px solid #833fb2",
                width: "50%",
                marginLeft: "0"
              }}
            />
            <h3>Address</h3>
            <p>VSS Nagar,Bhubaneswar,Odisha,India</p>
            <h3>Phone</h3>
            <p>(+91) 965-888-4153</p>
            <h3>Email</h3>
            <p>pravashbehera2@gamil.com</p>
          </Grid>
          <Grid
            style={{
              background: "#27221f",
              color: "white",
              padding: "1rem",
              borderRadius: "2%"
            }}
            item
            md={8}
            xs={12}>
            <h2>Education</h2>

            <Education
              startYear={2017}
              endYear={2021}
              schoolName="C.V Raman College of Engineering,Mahura,Bhubaneswar"
              schoolDescription="Bachelor of Computer Science and Engineering; Minor: English; GPA: 8.65, anticipated in May 2021"
            />
            <hr
              style={{
                border: "1px solid #e22947",
                width: "67%",
                marginLeft: "0"
              }}
            />
            <Education
              startYear={2013}
              endYear={2015}
              schoolName="Buxi Jagabandhu Bidyadhar College,Kalpana,Bhubaneswar"
              schoolDescription="Passed Out I.sc from CHSE Odisha Exam with 85% GPA with Combination with Pysics,Chemistry,Mathematics,Biology."
            />
            <hr
              style={{
                border: "1px solid #e22947",
                width: "67%",
                marginLeft: "0"
              }}
            />
            <Education
              startYear={2010}
              endYear={2013}
              schoolName="Vss Nagar Govt High School,VSS Nagar,Bhubaneswar"
              schoolDescription="Completed the Secondary education with 88% GPA in BSE Odisha Exam and got the 3rd Highest in My School."
            />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}

class Education extends React.Component {
  render() {
    return (
      <Grid container spacing={5}>
        <Grid item md={4} xs={12}>
          <p>
            {this.props.startYear} - {this.props.endYear}
          </p>
        </Grid>
        <Grid item md={8} xs={12}>
          <h6>{this.props.schoolName}</h6>
          <p>{this.props.schoolDescription}</p>
        </Grid>
      </Grid>
    );
  }
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CenteredGrid);
